.project-card {
  position: relative;
  padding: 30px;
  min-height: 300px;
  background: var(--accent-color);
  background-color: var(--main-bg-color);
  color: var(--dark-color);
  overflow: hidden;
  transition: background-color 0.3s ease-in-out;
}
.project-card__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
  transition: opacity 0.3s ease-in-out;
}
.project-card__title {
  margin-bottom: 40px;
}
.project-card__descr {
  margin: 0;
  margin-bottom: 30px;
  font-weight: 400;
  font-size: 20px;
  line-height: 130%;
}
.project-card__links-wrapper {
  display: flex;
  align-items: center;
}
.project-card__link:not(:last-child) {
  margin-right: 20px;
}
.project-card__link {
  display: block;
  padding: 10px 20px;
  border: 1px solid var(--accent-color);
  border-radius: 24px;
  font-size: 20px;
  line-height: 1;
