@media (max-width: 768px) {
  .project-card {
    padding: 20px 0;
  }
  .project-card__descr {
    font-size: 16px;
    margin-bottom: 20px;
  }
  .project-card__title {
    margin-bottom: 20px;
  }
  .project-card__link {
    font-size: 16px;
  }
